@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.Card {
    display: flex;
    flex-direction: column;
    margin: 10px;
}

.Card-held {
    opacity: 0.65;
}

.Card-img {
    width: 150px;
}

.Card-holdBtn {
    cursor: pointer;
    margin-top: 5px;
    color: white;
    background-color: green;
    border: none;
    padding: 10px 20px;
    font-size: 18px;
    box-shadow: 10px 10px 20px -15px rgba(0, 0, 0, 0.75);
}

.Card-holdBtn:active {
    transform: translate(1px, 1px);
    box-shadow: 10px 10px 20px -18px rgba(0, 0, 0, 0.75);
}

.Card-gameOverBtn {
    margin-top: 5px;
    color: white;
    background-color: red;
    border: none;
    padding: 10px 20px;
    font-size: 18px;
    box-shadow: 10px 10px 20px -15px rgba(0, 0, 0, 0.75);
}

@media only screen and (max-width: 1100px) {
    .Card-img {
        width: 120px;
    }

    .Card-holdBtn {
        padding: 10px 10px;
        font-size: 16px;
    }

    .Card-gameOverBtn {
        padding: 10px 10px;
        font-size: 16px;
    }
}

@media only screen and (max-width: 900px) {
    .Card-img {
        width: 100px;
    }
}

@media only screen and (max-width: 600px) {
    .Card-img {
        width: 90px;
    }
}

.Cards {
    display: flex;
    align-items: center;
    justify-content: center;
}

.Wager {
    display: flex;
    align-items: center;
    justify-content: center;
}

.Wager-area {
    display: flex;
    align-items: center;
    justify-content: space-evenly;
}

.Wager-amount {
    margin-right: 10px;
}
.Wager-button {
    cursor: pointer;
    display: inline-block;
    width: 50px;
    font-size: 22px;
    padding: 5px 10px;
    border-radius: 5px;
    background-color: rgb(255, 3, 3);
    /* border: 2px solid white; */
    color: white;
}

.Wager-button:disabled {
    background-color: rgb(248, 97, 97);
}

.PlayArea {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: rgba(0, 0, 0, 0.2);
}

.PlayArea-info {
    display: flex;
    align-items: center;
    justify-content: space-around;
    width: 100%;
}

.notcards {
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    width: 100%;
}

.notcards-item {
    margin: 0 50px;
    text-align: center;
}

.continue-btn {
    cursor: pointer;
    font-size: 18px;
    border: none;
    background-color: green;
    color: white;
    padding: 10px 25px;
    border-radius: 5px;
    box-shadow: 10px 10px 20px -15px rgba(0, 0, 0, 0.75);
}

.continue-btn:active {
    transform: translate(1px, 1px);
    box-shadow: 10px 10px 20px -18px rgba(0, 0, 0, 0.75);
}

.reload-btn {
    background-color: #555;
    color: white;
    cursor: pointer;
    font-size: 18px;
    border: none;
    padding: 10px 25px;
    border-radius: 5px;
    box-shadow: 10px 10px 20px -15px rgba(0, 0, 0, 0.75);
}

.RulesArea {
    display: flex;
    flex-direction: column;
    width: 100%;
    background-color: white;
    font-size: 20px;
    text-align: start;
    padding: 5px 0px;
}

.Rules-header {
    font-size: 22px;
    text-align: center;
    width: 100%;
    font-weight: bold;
}

.row {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: 0 12px;
    padding: 0 6px;
}

.winning {
    font-weight: bold;
    background-color: rgb(201, 255, 74);
}

.Game {
    border: 3px solid green;
    background-color: rgba(0, 0, 0, 0.1);
    padding: 25px;
    border-radius: 5px;
    font-family: "Montserrat", sans-serif;
}

.Game-title {
    background-color: rgb(187, 1, 1);
    color: white;
    padding: 10px 0 10px 0;
    margin: -5px 0 0 0;
}

.App {
    text-align: center;
    height: 100vh;
    /* background-image: url("./VP2/images/poker_bkgd.jpg");
    background-size: cover;
    background-position: bottom; */
    background-color: #eee;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}

